<template lang="pug">

  #media-wrapper(v-bind:class="{ 'soundcloud-mode': (src && playerType === 'soundcloud'), 'youtube-mode': (src && playerType === 'youtube') }")

    #video-wrapper
      youtube#video-container(v-if="src && (this.playerType === 'youtube')" v-bind:video-id="src" v-bind:player-vars="{'autoplay': 1, 'controls': 0, 'playsinline': 1, 'rel': 0, 'showinfo': 0, 'modestbranding': 1}" @ready="youtubeReady" @playing="youtubePlaying" @paused="youtubePaused" @ended="youtubeEnded" @buffering="youtubeBuffering" v-bind:player-width="pWidth" v-bind:player-height="pHeight" v-bind:style="{ height: `${this.pHeight}px`, width: `${this.pWidth}px` }")
      #soundcloud-container(v-if="src && (this.playerType === 'soundcloud')" v-bind:style="{ height: `${this.pHeight}px`, width: `${this.pHeight}px` }")
        img(src="https://developers.soundcloud.com/assets/logo_big_white-65c2b096da68dd533db18b9f07d14054.png")
    
    #images-wrapper
      img.image-tile(v-for="(mediaItem, index) in mediaItems" v-bind:src="mediaItem" v-bind:class="{ active: (currentSegmentIndex === index) }")
      .clearfix

</template>

<script>
const SYNC_THRESHOLD = 2.0;

import _ from 'lodash';
import { mapGetters } from 'vuex';
import * as types from '@/store/mutation-types';

import SoundCloud from 'soundcloud';

export default {
  name: 'media-container',
  props: ['player-type'],
  mounted() {
    alert('mounted');
    SoundCloud.initialize({
      client_id: '3b3b0aa8045ca036d24515344ae0d60b',
    });
    SoundCloud.stream(this.src).then((newPlayer) => {
      console.log(newPlayer);
      this.soundcloudPlayer = newPlayer;
    });
  },
  data() {
    return {
      soundcloudPlayer: undefined,
      pHeight: 188,
      pWidth: (188 / 0.5625),
    };
  },
  watch: {
    playerType(nV, oV) {

      if (oV === 'soundcloud') {
        // alert('pausing sc');
        this.soundcloudPlayer.pause();
      }

      if (nV === 'soundcloud') {
        // SoundCloud.stream(this.src).then((newPlayer) => {
          // this.soundcloudPlayer = newPlayer;
          this.soundcloudPlayer.play();
        // });
      }
    },
    currentTime(nV, oV) {
      switch (this.playerType) {
        case 'youtube':
          this.youtubeSeek(this, nV);
          break;
        case 'soundcloud':
          this.soundcloudSeek(nV);
          break;
      }
    },
    videoPlaying(nv, oV) {
      if ((this.player || this.soundcloudPlayer) && this.src) {
        if (nv) {
          this.$log.log('play');
          switch (this.playerType) {
            case 'youtube':
              this.player.playVideo();
              break;
            case 'soundcloud':
              this.soundcloudPlayer.play();
              break;
          }
        } else {
          this.$log.log('pause');
          switch (this.playerType) {
            case 'youtube':
              this.player.pauseVideo();
              break;
            case 'soundcloud':
              this.soundcloudPlayer.pause();
              break;
          }
        }
      }
    },
  },
  methods: {
    change() {},
    youtubeReady(player) {
      this.player = player;
      this.player.seekTo(this.currentTime);
      this.$store.commit(types.VIDEO_READY);
    },
    youtubePlaying(player) {
      this.$store.commit(types.PLAY_VIDEO);
    },
    youtubeEnded() {
      this.$store.commit(types.PAUSE_VIDEO);
    },
    youtubeBuffering() {
      // this.$store.commit(types.PAUSE_VIDEO);
    },
    youtubePaused() {
      this.$store.commit(types.PAUSE_VIDEO);
    },
    youtubeSeek: _.throttle(function(self, position) {

      console.log('youtubeSeek', position);
      if (!self.player) { return; }

      const playerTime = self.player.getCurrentTime();
      if (!playerTime) { return; }
      const outOfSync = ((self.currentTime < (playerTime - SYNC_THRESHOLD))
      || (self.currentTime > (playerTime + SYNC_THRESHOLD)));

      if (outOfSync) {
        self.$log.log('Video out of sync - seeking');
        self.player.seekTo(position);
      }
    }, 200),
    soundcloudSeek(position) {

      console.log('soundcloudSeek', position);
      if (!this.soundcloudPlayer) { return; }

      try {
        const playerTime = this.soundcloudPlayer.currentTime() / 1000;
        console.log(playerTime);
        console.log(this.currentTime);
        const outOfSync = ((this.currentTime < (playerTime - SYNC_THRESHOLD)) || (this.currentTime > (playerTime + SYNC_THRESHOLD)));

        if (outOfSync) {
          this.$log.log('Audio out of sync - seeking');
          this.soundcloudPlayer.seek(position * 1000);
        }
      } catch (Exception) {
      }
    }
  },
  computed: {
    ...mapGetters([
      'videoIsActive', 'videoEnabled', 'currentTime', 'currentSection', 'videoPlaying', 'media', 'currentSegment',
    ]),
    src() {
      switch (this.playerType) {
        case 'youtube':
          return (this.currentSection) ? this.currentSection.videoId : '';
          break;
        case 'soundcloud':
          return '/tracks/336063279';
          break;
      }
    },
    mediaItems() {
      let mediaItems = [];

      if (this.media.length === 0) { return mediaItems; }

      let media = `https://github.com/connectedacademy/testclass/raw/master/course/content/en/class1/transcripts/${this.media[0].text}`;

      for (var i = 0; i < this.media.length; i++) {
        const image = this.media[i];
        media = `https://github.com/connectedacademy/testclass/raw/master/course/content/en/class1/transcripts/${image.text}`
        mediaItems.push(media);
      }

      return mediaItems;
    },
    currentSegmentIndex() {
      if (this.media.length === 0) { return 0; }

      let media = `https://github.com/connectedacademy/testclass/raw/master/course/content/en/class1/transcripts/${this.media[0].text}`;

      for (var i = 0; i < this.media.length; i++) {
        const image = this.media[i];
        if (this.currentSegment > (image.start) && this.currentSegment < (image.end)) {
          return i;
        }
      }
    },
  },
};
</script>

<style lang="stylus" scoped>

@import '~stylus/shared'

$media-height = 220px

#media-wrapper
  animate()
  padding 0 8px
  position relative
  height $media-height
  overflow-x scroll
  overflow-y hidden
  &.youtube-mode
    padding-left (188px / 0.5625) + 16px
  &.soundcloud-mode
    padding-left 188px + 16px
  #images-wrapper
    background white
    padding 8px
    white-space nowrap
    .image-tile
      animate()
      height ($media-height - 32px)
      margin 8px
      opacity 0.5
      width auto
      &.active
        opacity 1

  #video-wrapper
    top 0
    bottom 0
    left 0
    right auto
    padding 16px
    position absolute
    #soundcloud-container
      radius(50%)
      background-color #FF5500
      img
        position absolute
        top 50%
        left 50%
        transform translate(-50%, -50%)
    #video-container
      top 16px
      left 16px
      opacity 1
      box-sizing border-box
      overflow hidden

      iframe, object, embed, video
        position absolute !important
        top 0 !important
        bottom 0 !important
        left 0 !important
        right 0 !important

</style>



